a {
  font: inherit;
  text-decoration: inherit;
  font-style: inherit;
  color: inherit;
  width: fit-content;
  flex-direction: column;
  display: flex;
  width: fit-content;
  margin: 8px 40px;
  margin-left: 0px!important;
  margin-top: 0px;

  .MovieCard {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    
    
    .card {
      display: flex;
      width: fit-content;
      flex-direction: column;
      width: min-content; 
      &:hover{
        .cardImg{
          background-size: 125% !important;
          transition: all 200ms;
        }
      }


      .cardImg {
        background-size: 120%;
        background-position: center center;
        aspect-ratio: 9 / 16;
        width: 280px;
        border-radius: 8px;
        margin-bottom: 20px;
        transition: all 200ms;
      }

    }
    h2{
      hyphens:auto;
      display: flex;
      flex-wrap: wrap;
      text-overflow: clip;
    }
    span{
      display: flex;
    }


  }
}


@media screen and (max-width: 768px){
  .MovieCard{
   .cardImg{
     width: 160px !important;
   }
  }
}









