a {
  font: inherit;
  text-decoration: inherit;
  font-style: inherit;
  color: inherit;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: fit-content;
  margin: 8px 40px;
  margin-left: 0px !important;
  margin-top: 0px;
}

a .MovieCard {
  height: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

a .MovieCard .card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

a .MovieCard .card:hover .cardImg {
  background-size: 125% !important;
  -webkit-transition: all 200ms;
  transition: all 200ms;
}

a .MovieCard .card .cardImg {
  background-size: 120%;
  background-position: center center;
  aspect-ratio: 9 / 16;
  width: 280px;
  border-radius: 8px;
  margin-bottom: 20px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
}

a .MovieCard h2 {
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-overflow: clip;
}

a .MovieCard span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

@media screen and (max-width: 768px) {
  .MovieCard .cardImg {
    width: 160px !important;
  }
}
