#Search {
  margin-top: 32px;


  form {
    height: 32px;
    padding-bottom: 14px;
    width: fit-content;
    display: flex !important;
    flex-direction: row;
    align-items: center;
    gap: 24px;


    label{
      width: fit-content;
      height: fit-content;
    }
      &::focus{
        i{
          display: none;
        }
      }



    i {
      color: white;
      display: flex;


    }

    input {
      border: none;
      color: white;
      display: flex;
      width: fit-content;
      font-family: 'Outfit';
      font-style: normal;
      font-weight: 300;
      font-size: 24px;
      line-height: 30px;
      background: none;
      width: 1000px;
      caret-color:  red  ;
      color: white;
     
      

      &:focus{
        // border: none !important;
        border-bottom: 1px solid #5A698F !important; 
        
      }



    }

  }

}