header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 75px;
  background-color: #161D2F;
  border-radius: 20px;
  padding: 32px;
  margin-right: 0px !important;
  max-height: 100vh !important; 



  Link {
    width: fit-content;
  }

  .Logo {
    color: #FC4747;
    width: fit-content;
    margin: 0px auto;

  }

  nav {
    display: flex;
    align-items: center;
    height: fit-content;




    ul {
      display: flex;

      flex-direction: column;
      list-style: none;
      justify-content: center;
      height: fit-content;
      gap: 40px;
      padding: 0px;
      margin: 0px auto;
      

        li {
     
        a{
          width: fit-content;
          margin: 0;
          .fa-xl{
            line-height: 24px;
            color: #5A698F;
            width: fit-content;
            transition: all 200ms;
            &:hover {
              color: #FC4747;
              transition: all 200ms;
              transform: scale(1.1);
            }
          }
        }
      
      } 
    }
  }
}

@media screen and (max-width: 768px){
  header{
    flex-direction: row;
    width: 100%;
    align-items: center;
    
    gap: 0px;
    ul{
      flex-direction: row !important;
      align-items: center ;
      margin: auto;

    }
    height: fit-content;
  }
}