#MoviePage {
  color: white;
  width: 100%;
  height: 100% !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  display: flex;


  .video {

    height: auto;
    aspect-ratio: 16/9;
    position: absolute;
    width: 60%;
    left: 20%;
    z-index: -1;
    border-radius: 16px;
  }


  .movieInfo {

    justify-content: space-between;
    gap: 150px;
    padding: 50px;
    backdrop-filter: blur(40px);
    border-radius: 16px;
    position: relative;
    margin: auto;
    display: flex;
    top: 50px;

    img {

      height: 400px;
      width: auto;
      border-radius: 16px;
    }

    article {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;



      p {
        width: 400px;
        position: relative;
        bottom: 40px;
      }

      .inFav {
        grid-area: inFav;
      }
    }

  }



}

@media screen and (max-width: 768px) {
  #MoviePage {
    display: flex;
    flex-direction: column;
    width: 100%;

    .movieInfo {
      flex-direction: column;
      align-items: center;
      gap: 20px;
      padding: 20px;

      article {
        gap: 20px;

        p {
          width: 100%;
        }
      }

    }

    .video {
      position: relative;
      width: 100%;
      margin: 0 auto;
      height: auto;
      left: 0px;
    }
  }



}