@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@200;300;400;500;600;700;800;900&family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  // border:  1px solid white;
  margin: 0;
  padding: 0%;
  font: inherit;
  box-sizing: border-box;
max-width: 100%;

}

body {
  background-color: #10141E;
  width: 100vw;

  #root {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 80px;
    width: 100%;
    padding: 64px;
    align-content: stretch;
    align-items: stretch;

    main {
      display: inline;
      border-radius: 8px;
      overflow-x: hidden !important;
      width: 100%;
      height: 100%;

    }







  }
}
@media screen and (max-width: 768px){
  #root{ 
    flex-direction: column !important;
    padding: 20px !important;
    gap: 10px !important;
  }
}




