#MoviePage {
  color: white;
  width: 100%;
  height: 100% !important;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: flex;
}

#MoviePage .video {
  height: auto;
  aspect-ratio: 16/9;
  position: absolute;
  width: 60%;
  left: 20%;
  z-index: -1;
  border-radius: 16px;
}

#MoviePage .movieInfo {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  gap: 150px;
  padding: 50px;
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  border-radius: 16px;
  position: relative;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: 50px;
}

#MoviePage .movieInfo img {
  height: 400px;
  width: auto;
  border-radius: 16px;
}

#MoviePage .movieInfo article {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

#MoviePage .movieInfo article p {
  width: 400px;
  position: relative;
  bottom: 40px;
}

#MoviePage .movieInfo article .inFav {
  grid-area: inFav;
}

@media screen and (max-width: 768px) {
  #MoviePage {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }
  #MoviePage .movieInfo {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 20px;
    padding: 20px;
  }
  #MoviePage .movieInfo article {
    gap: 20px;
  }
  #MoviePage .movieInfo article p {
    width: 100%;
  }
  #MoviePage .video {
    position: relative;
    width: 100%;
    margin: 0 auto;
    height: auto;
    left: 0px;
  }
}
