#Search {
  margin-top: 32px;
}

#Search form {
  height: 32px;
  padding-bottom: 14px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 24px;
}

#Search form label {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

#Search form::focus i {
  display: none;
}

#Search form i {
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

#Search form input {
  border: none;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: 'Outfit';
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  background: none;
  width: 1000px;
  caret-color: red;
  color: white;
}

#Search form input:focus {
  border-bottom: 1px solid #5A698F !important;
}
